import * as React from 'react'
import Page from '../components/Page'
import { NextContext } from 'next'
import Post from '../components/Post'
import { Posts } from '../data/PostMenu'
import dynamic from 'next/dynamic'
// import Router from 'next/router'

class DocPage extends React.Component<{ title: keyof typeof Posts, content: string, demos: React.FunctionComponent }> {
  static getInitialProps = async function ({ query }: NextContext<{ title: string }>) {
    let content: any
    let demos: any
    try {
      content = await import(`../markdowns/${query.title}.md`)
      content = content.default
      demos = dynamic(() => import(`../demos/button`))
      // demos = demos.default
    } catch{
      // Router.replace('')
      console.log('加载失败')
    }
    return { title: query.title, content, demos }
  }
  render() {
    const { title, content, demos } = this.props
    const Demos = demos
    return (<Page title={`${Posts[title].subTitle ? Posts[title].subTitle + ' ' : ''}${Posts[title].title} - Semon UI`}>
      <Post>{content}</Post>
      <Post>
        <h2>代码演示</h2>
        <Demos></Demos>
      </Post>
    </Page>
    )
  }
}

export default DocPage
